<template>
  <div
    class="
      w-1/2
      grid
      sm:grid-cols-2
      md:grid-cols-3
      lg:grid-cols-4
      gap-4
      text-center
      mb-3
    "
  >
    <div
      v-for="item in filteredData"
      :key="item.id"
      class="shadow shadow-slate-400 p-3 rounded-md flex flex-col items-center"
    >
      <h4 class="mb-2 font-rubik">{{ item.name }}</h4>
      <div class="flex flex-col items-end mr-1">
        <p>מחיר : {{ item.price }} שקלים</p>
        <h4>
          ימי הכנה :
          <span v-if="!item.times.makeDays">מיידי</span>
          <span v-if="item.times.makeDays === 1">יום אחד</span>
          <span v-if="item.times.makeDays > 1"
            >{{ item.times.makeDays }} ימים</span
          >
        </h4>
        <div class="flex flex-wrap flex-row-reverse ">
          <h4> : זמין בימים</h4>
          <h4 class="mr-1" v-for="day in item.times.availableDaysOfWeek" :key="day">
            {{ weekDays[day - 1] }} <span v-if="item.times.availableDaysOfWeek.length>1">/</span>
          </h4>
        </div>
      </div>
      <img
        class="p-2 md:w-32 lg:w-36 xl:w-48"
        :src="item.imgUrl"
        :alt="item.name"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["filteredData"],
  data() {
    return {
      weekDays: ["ראשון", "שני", "שלישי", "רביעי", "חמישי", "שישי", "שבת"],
    };
  },
};
</script>